<template>
  <div>
    <el-menu
        class="el-menu-homepage"
        mode="horizontal"
        background-color="#1F4B62"
        text-color="#fff"
        active-text-color="#ffd04b"
        style="text-align: left">
      <el-tooltip class="item" effect="dark" content="点击回到主页" placement="bottom">
        <el-menu-item index="10" @click="backtoHomepage()" style="width: 180px;float: left">
          回到主页
        </el-menu-item>
      </el-tooltip>
    </el-menu>
    <el-container style="height: calc(100vh - 80px); border: 1px solid #eee">
      <el-header width="200px" style="background-color: rgb(238, 241, 246)">
            <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
              <el-submenu index="1">
                <template v-slot:title><i class="el-icon-message"></i>何</template>
                <el-menu-item-group>
                  <router-link to="/personinfo"><el-menu-item index="1-1">用户信息</el-menu-item></router-link>
                  <router-link to="/changeinfo"><el-menu-item index="1-2">修改信息</el-menu-item></router-link>
                </el-menu-item-group>
              </el-submenu>
                  <el-submenu index="10">
                    <template v-slot:title>
                      我的订单
                    </template>
                    <el-menu-item index="20">
                      <router-link to="/ormanage">
                        全部订单
                      </router-link>
                    </el-menu-item>
                    <el-menu-item index="30">
                      <router-link to="/orderwaitpay">
                        待付款订单
                      </router-link>
                    </el-menu-item>
                    <el-menu-item index="40">
                      <router-link to="/orderwillcancel">
                        已付款订单
                      </router-link>
                    </el-menu-item>
                    <el-menu-item index="50">
                      <router-link to="/ordercheckedin">
                        已入住订单
                      </router-link>
                    </el-menu-item>
                    <el-menu-item index="60">
                      <router-link to="/ordercanceled">
                        已退房订单
                      </router-link>
                    </el-menu-item>
                  </el-submenu>
                  <router-view/>
            </el-menu>
            <router-view/>
      </el-header>
      <el-container>
        <el-main  style="position: relative; width: 70%;left: 15%">
          <el-table :data="tableData" empty-text="无此类订单">
            <el-table-column prop="o_id" label="订单号" width="140">
            </el-table-column>
            <el-table-column prop="u_id" label="用户id" width="140">
            </el-table-column>
            <el-table-column prop="hname" label="酒店id" width="140">
            </el-table-column>
            <el-table-column prop="rtname" label="房型id" width="140">
            </el-table-column>
            <el-table-column prop="Create_time" label="创建日期" width="200">
            </el-table-column>
            <el-table-column prop="state" label="订单状态" width="80">
            </el-table-column>
            <el-table-column prop="option" label="操作" width="200">
              <template v-slot="scope">
                <el-button type="warning" size="mini" @click="payorder(scope.row)">立即支付</el-button>
                <el-button type="info" size="mini" @click="cancel(scope.row)">取消</el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import EDesc from "@/pages/e-desc";
import EDescItem from "@/pages/e-desc-item";
export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: "orderwaitpay",
  data () {
    return {
      tableData: [{
        o_id: '210615001',
        u_id: '2019141460096',
        h_id: '103872',
        hname: '成都世纪城天堂洲际大饭店',
        rtname:'大床房',
        Create_time: '2021-06-15 14:04:12',
        checkin_time: '2021-06-25 14:04:14',
        checkout_time: '2021-06-30 14:04:17',
        state: '待支付',
        total_price: '3000',
        rating:"5",
        review: '舒适，但床在摇晃',
        pname1: "何翔龙",
        pname2:"凤姐",
        p1_tel:"12124404047",
        p2_tel:"12147752774",
      },
        {
          o_id: '210615001',
          u_id: '2019141460096',
          h_id: '103872',
          hname: '成都世纪城天堂洲际大饭店',
          rtname:'大床房',
          Create_time: '2021-06-15 14:04:12',
          checkin_time: '2021-06-25 14:04:14',
          checkout_time: '2021-06-30 14:04:17',
          state: '待支付',
          total_price: '3000',
          rating:"5",
          review: '舒适，但床在摇晃',
          pname1: "何翔龙",
          pname2:"凤姐",
          p1_tel:"12124404047",
          p2_tel:"12147752774",
        }],

    }
  },
  methods: {
    backtoHomepage () {
      this.$router.push('/homepage')
    },
    getnotpaiedorderinfo () {
      const self = this
      this.$axios({
        url: '/api/orderList/ListByState',
        method: 'POST',
        params: {
          pageNo: 1,
          pageSize: 10,
          state: '待支付'
        }
      }).then(
          successResponse => {
            if (successResponse.data.data) {
              self.tableData = successResponse.data.data
              // console.log(self.tableData)
              // eslint-disable-next-line no-empty
            } else {
            }
          }).catch(err => {
        console.log(err, 'err')
      })
    },
    openSuccess () {
      this.$message({
        showClose: true,
        message: '支付成功',
        type: 'success'
      })
    },
    openFail () {
      this.$message({
        showClose: true,
        message: '支付失败',
        type: 'error'
      })
    },
    opencancelSuccess () {
      this.$message({
        showClose: true,
        message: '订单待取消，请等待审核',
        type: 'success'
      })
    },
    opencancelFail () {
      this.$message({
        showClose: true,
        message: '订单取消失败',
        type: 'error'
      })
    },
    payorder (value) {
      // console.log(value.orderid)
      const self = this
      this.$axios({
        url: '/api/orderList/UpdateState',
        method: 'POST',
        params: {
          o_id: value.o_id,
          newstate: '已付款'
        }
      }).then(
          successResponse => {
            self.openSuccess()
          }).catch(err => {
        self.openFail()
        console.log(err, 'err')
      })
      this.$router.go(0)
    },
    cancel (value) {
      const self = this
      this.$axios({
        url: '/api/orderList/UpdateState',
        method: 'POST',
        params: {
          o_id: value.o_id,
          newstate: '待取消'
        }
      }).then(
          successResponse => {
            self.opencancelSuccess()
          }).catch(err => {
        self.opencancelFail()
        console.log(err, 'err')
      })
      this.$router.go(0)
    }
  },
  created () {
    // console.log('获取待支付订单')
    this.getnotpaiedorderinfo()
  }
}
</script>

